x: 1;\r\n  top: 0;\r\n  position: fixed;\r\n  text-align: center;\r\n  margin-top: 60px;\r\n  background-color: white;\r\n}\r\n\r\n.nav {\r\n  width: 100%;\r\n  margin-left: auto;\r\n  margin-right: auto;\r\n  text-align: center;\r\n  position: fixed;\r\n  top: 0;\r\n  margin-top: 60px;\r\n  background-color: #fff;\r\n  border-bottom: 1px solid black;\r\n}\r\n\r\n.nav__list {\r\n  width: 100%;\r\n  height: auto;\r\n  padding: 0;\r\n  margin: 0;\r\n  \r\n}\r\n\r\n.nav__item {\r\n  box-sizing: border-box;\r\n  display: inline-block;\r\n  width: 24%;\r\n  text-align: center;\r\n  line-height: 24px;\r\n  text-transform: uppercase;\r\n}\r\n\r\n.nav a {\r\n  display: inline-block;\r\n  padding: 1.3em;\r\n  text-decoration: none;\r\n  color: #616161;\r\n  z-index: 5;\r\n\r\n}\r\n\r\n.nav a:hover {\r\n  text-decoration: underline;\r\n  color: #000;\r\n}\r\n\r\n.nav a:focus {\r\n  border : 2px solid blue;\r\n  outline: none;\r\n}\r\n\r\n/* \r\nHERO\r\n */\r\n\r\n.hero {\r\n  display: flex;\r\n  align-items: center;\r\n  min-height: 500px; \r\n  width: 100%;\r\n  text-align: center;\r\n  background-position: center;\r\n  background-size: cover;\r\n  margin-top: 115px;\r\n  background-repeat: no-repeat;\r\n}\r\n\r\n\r\n.hero__inner {\r\n  margin: 0 auto;\r\n  max-width: 800px;\r\n}\r\n\r\n.hero__title {\r\n  color: #fff;\r\n  font-weight: 500;\r\n  font-size: 36px;\r\n}\r\n\r\n.hero__tagline {\r\n  color: #fff;\r\n  margin-top: 16px;\r\n  font-size: 18px;\r\n  font-weight: 300;\r\n  margin: 10px;\r\n}\r\n\r\n#explore_btn {\r\n  background-color: #1A120B; \r\n  color: #fff;\r\n  padding: 10px 20px;\r\n  margin-top: 30px;\r\n  border: none; \r\n  border-radius: 5px;\r\n  cursor: pointer; \r\n  font-size: 16px;\r\n  font-weight: bold; \r\n  transition: background-color 0.3s;\r\n  height: 44px;\r\n  min-width: 44px;\r\n\r\n}\r\n\r\n#explore_btn:hover {\r\n  background-color: #fff; \r\n  color: #1A120B;\r\n}\r\n\r\nbutton#explore_btn:focus {\r\n  border : 2px solid blue;\r\n  outline: none;\r\n  display: inline-block;\r\n}\r\n\r\n/* MAIN */\r\n#penunjuk {\r\n  position: absolute;\r\n  top: 518px;\r\n  color: transparent;\r\n  z-index: -2000;\r\n}\r\n\r\nmain {\r\n  flex: 1;\r\n  width: 100%;\r\n}\r\n\r\n/* RESTO LIST */\r\nh1.fav {\r\n  margin-top: 60px; /* Mengurangi margin top */\r\n  text-align: center;\r\n}\r\n\r\n.list_heading {\r\n  text-align: center;\r\n  font-size: 36px;\r\n  margin-bottom: 10px;\r\n  margin-top: 20px;\r\n}\r\n\r\n.star {\r\n  color: rgba(208, 120, 43, 0.889);\r\n}\r\n\r\n.rating {\r\n  font-size: 16px;\r\n  font-weight: bold;\r\n}\r\n\r\n.restaurant {\r\n  background-color: #fff;\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  text-align: center;\r\n  padding: 10px;\r\n  margin: 20px auto; /* Mengubah margin dan menambah auto agar restoran berada di tengah */\r\n  width: 100%;\r\n  max-width: 300px;\r\n  height: 95%;\r\n}\r\n\r\n#restaurant-list {\r\n  margin: 20px;\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  justify-content: center;\r\n  gap: 20px;\r\n  \r\n}\r\n\r\n\r\n.restaurant:focus {\r\n  background-color: #d5cea338;\r\n  border: 20px #1A120B;\r\n}\r\n\r\np.city {\r\n  text-align: center;\r\n  font-size: 14px;\r\n  color: #1a120b74;\r\n}\r\n\r\n.desc {\r\n  padding: 5px;\r\n}\r\n\r\np {\r\n  padding: 5px;\r\n  text-align: justify;\r\n}\r\n\r\n.restaurant img.lazyloaded {\r\n  width: 100%;\r\n  max-height: 150px;\r\n}\r\n/* style untuk page detail */\r\n/* \r\nRESTO DETAIL\r\n */\r\n#restaurant-detail {\r\n  display: grid;\r\n  grid-template-columns: repeat(1, 1fr); \r\n  gap: 5px;\r\n  text-align: justify;\r\n  justify-items: center; \r\n}\r\n\r\n#restaurant-detail img.lazyloaded {\r\n  width: 50%;\r\n  height: fit-content;\r\n}\r\n\r\n.restaurant-box {\r\n  background-color: #fff;\r\n  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.4);\r\n  border-radius: 8px;\r\n  overflow: hidden;\r\n  text-align: center;\r\n  padding: 10px;\r\n  max-width: 800px;\r\n  margin: 