\"] {\r\n  font-weight: bold;\r\n  display: block;\r\n  margin-bottom: 5px;\r\n}\r\n\r\ninput#name {\r\n  width: 100%;\r\n  padding: 8px;\r\n  margin-bottom: 10px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\n\r\nlabel[for=\"review\"] {\r\n  font-weight: bold;\r\n  display: block;\r\n  margin-bottom: 5px;\r\n}\r\n\r\ntextarea#review {\r\n  width: 100%;\r\n  padding: 8px;\r\n  margin-bottom: 10px;\r\n  border: 1px solid #ccc;\r\n  border-radius: 4px;\r\n  resize: vertical;\r\n  min-width: 44px;\r\n  min-height: 44px; \r\n}\r\n\r\nbutton#submitReview {\r\n  background-color: rgba(157, 104, 58, 0.889);\r\n  color: #fff;\r\n  border: none;\r\n  border-radius: 4px;\r\n  padding: 10px 20px;\r\n  font-size: 12px;\r\n  cursor: pointer;\r\n  min-width: 44px;\r\n  min-height: 44px;\r\n}\r\n\r\nbutton#submitReview:hover {\r\n  background-color: rgba(78, 52, 29, 0.889);\r\n}\r\n\r\n/* \r\nfooter \r\n*/\r\nfooter {\r\n  background-color: #1A120B;\r\n  padding: 50px;\r\n  width: 100%;\r\n  text-align: center;\r\n  bottom: 0;\r\n  color: #ffffffad;\r\n}"],"sourceRoot":""}]);
